nav{
    background: rgba(252, 254, 254, 0.3);
    width: max-content;
    display: block;
    padding: .7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: .8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a{
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;

color: black;
font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,.3);
    
}

nav a.active{
    background: var(--color-bg);
    color: var(--color-white);
}