@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;1,600&display=swap');

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root{

  
/* --color-bg:#1f1f38;
--color-bg-variant:#2c2c6c;
--color-primary:#4db5ff;
--color-primary-variant:rgba(77,181,225,.4);
--color-white:#fff;
--color-light:rgba(225,225,225,.6); */

  --color-bg:#000000; 
  --color-bg-variant:#002B5B;
  --color-primary:#e8be13;
  --color-primary-variant:#cad2c5;
  --color-white:#ffffff;
  --color-light:rgba(203, 209, 203, 0.6);

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background: var(--color-bg);
  /* background-color: #2c2c6c; */

  

  color: var(--color-white);
  line-height: 1.7;
  /* background-image: url(/src/Assets/bg-texture.png);
 */
}

/*General STyles */

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  

}
section h2,section h5{
  text-align: center;
  color: var(--color-light);
}

section h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: .75rem 1.2rem;
  border-radius: .4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
   background: var(--color-white);
   color: var(--color-bg);
   border-color: transparent;

}


.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}


/*media queries for medium devices */

@media screen and (max-width:1024px){
  .container{
    width: var(--container-width-md);

  }
section{
  margin-top: 6rem;
}

}

/*media queries for small devices */

@media screen and (max-width:600px){
  .container{
    width: var(--container-width-sm);

  }
section >h2{
  margin-bottom: 2rem;
}

}